<template lang="">
<div style="height:100%;background:#fff">

       <div class="financial-box market-table-header">
        <div>
          <div class="nhsyl"></div>
          <div class="nhsyl-text"></div>
        </div>
        <div class="text-right-imp">
          <div>最新</div>
        </div>
        <div class="text-right-imp">
          <div>涨跌</div>
        </div>
        <div class="text-right-imp">
          <div>昨收</div>
        </div>
      </div>

      <ul class="market-table" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10" infinite-scroll-immediate-check=false>
        <li v-for="(item,index) in dataListJj" @click="goDetail(index)">

          <div class="financial-box">
            <div>
              <div class="nhsyl">{{ item.hqzqjc}}</div>
              <div class="nhsyl-text">{{ item.hqzqdm}}</div>
            </div>
            <div class="text-right-imp">
              <div>{{ zx(item) | toMoney}}</div>
            </div>
            <div class="text-right-imp">
              <div :class="styleColor(item.hqzjcj,item.hqzrsp)">{{zdfunc(index)}}</div>
            </div>
            <div class="text-right-imp">
              <div>{{item.hqzrsp | toMoney}}</div>
            </div>
          </div>
        </li>
      </ul>
      </ul>
      <div class="jqqd" v-if="dataListJj.length==0">
       {{tipMessage}}
      </div>

</div>
</template>
<script>
import custMixin from  'util/custMixin'
import {
  Navbar,
  TabItem,
  InfiniteScroll
} from 'mint-ui';
import {
  Toast
} from "mint-ui";
Vue.component(Navbar.name, Navbar);
Vue.component(TabItem.name, TabItem);
Vue.use(InfiniteScroll);
export default {
  data() {
    return {
    	tipMessage:"加载中...",
      loading: false,
      dataListJj: [],
      indexNow: '',
      pars: {
        epsBgn: "",
        epsEnd: "",
        hqzqdm: "",
        industry: "",
        offset: 0,
        pageSize: global.pageSize,
        recNo: 1,
        regCapitalBgn: "",
        regCapitalEnd: "",
        region: "",
        sortFields: "",
        hqscdm: global.scdm,
      }
    }
  },
  created() {
    this.marketJj();
  },
      mixins:[custMixin],
  computed: {},
  methods: {
      zx:function(item){
          return item.hqzjcj==0 ?"--": item.hqzjcj;
      },
    marketJj:function(){
      let self = this;
        Object.assign(this.pars,{xxzqzt:global.ZQZT.YXSC});
       axiosUtil.marketYx(this.pars).then(function(data) {
      var data = data.data;
      self.dataListJj= data.rows;
       if(self.dataListJj.length==0){
        self.tipMessage="暂无数据"
      }
    })
    },
    goDetail: function(index) {
         this.$router.push("/trade?hqzqdm=" + this.dataListJj[index].hqzqdm);
    },
    zdfunc: function(params) {//涨停
      var data=this.dataListJj;
      if(data.length!==0){
        var row = data[params];
      return parseFloat(row.hqzjcj) == 0 ? "--" : (parseFloat(parseFloat(row.hqzjcj).toFixed(4)).subtr(parseFloat(row.hqzrsp)));
    //   .div(parseFloat(row.hqzrsp))
      }

    },
    loadMore() {
      let self = this;
      this.loading = true;
      this.pars.recNo = this.pars.recNo + 1;
      axiosUtil.quote(this.pars).then(function(data) {
        var data = data.data;

        self.dataListJj = self.dataListJj.concat(data.rows);
        self.loading = false;
        if (data.total == self.dataListJj.length) {
          self.loading = true;
          Toast({
            message: "已加载最后一条",
            position: "bottom",
            duration: 1000
          });
        }

      })
    }
  }
}
</script>
<style media="screen" scoped >
.financial-box {
  display: -webkit-flex;
  /* Safari */
  display: flex;
}

.financial-box>div {
  flex-grow: 1;
  width: 33.33%;
}

.market-table-header {
  width: 90%;
  margin: 0 auto;
  margin-top: 6px;
  font-size: 12px;
  color: #999;
  padding: 12px 0;
  border-bottom: 1px solid #eee;
}

.market-table li {
  padding: 12px;
  border-bottom: 1px solid #eee;
}

.nhsyl-text {
  font-size: 14px;
  color: #999;
}

.mint-tab-container {
  height: 100%;
  padding-bottom: 41px;
}

.my-tab-container2 {
  height: 100%;
}
</style>
<style media="screen">
.mint-tab-container-wrap {
  height: 100%;
}

 ::-webkit-scrollbar {
  width: 0px;
  height: 1px;
}

 ::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}
</style>



